import React, { useState, useEffect } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, message } from 'antd';
import { TableData, ColEditDelete, CartHeadAction } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { getSubjectTypes, deleteSubjectType } from 'services/subjectTypeService';

const SubjectType = () => {
  const [reload, setReload] = useState(0);

  useEffect(() => {}, []);

  const handleDelete = async id => {
    try {
      await deleteSubjectType(id);
      message.success('Xóa thành công');
      setReload(reload + 1);
    } catch (err) {
      console.log(err);
    }
  };

  const getSubjectTypesWithIndex = async () => {
    const res = await getSubjectTypes();
    let index = 0;
    res.data.items.forEach(item => {
      index += 1;
      item.index = index;
    });
    return res;
  };

  const columns = [
    {
      title: 'STT',
      dataIndex: 'index',
      key: 'index',
      width: '100px',
    },
    {
      title: 'Loại môn học',
      dataIndex: 'label',
      key: 'label',
    },
    {
      title: 'Thao tác',
      dataIndex: 'operation',
      render: (text, record) => (
        <ColEditDelete
          linkEdit={{
            to: {
              pathname: `/subject-type/edit/${record.id}`,
            },
          }}
          onConfirmDelete={() => handleDelete(record.id)}
        />
      ),
    },
  ];

  return (
    <Card title="Loại môn học">
      <CartHeadAction>
        <BtnAddNew href="#/subject-type/create" />
      </CartHeadAction>
      <Row>
        <Col span={24}>
          <TableData
            reload={reload}
            columns={columns}
            service={getSubjectTypesWithIndex}
            rowKey="id"
          />
        </Col>
      </Row>
    </Card>
  );
};

export default props => (
  <Page {...props}>
    <SubjectType />
  </Page>
);



